// GNOME / Adwaita color palette
:root {
  --blue-1: #99c1f1; --blue-2: #62a0ea; --blue-3: #3584e4; --blue-4: #1c71d8; --blue-5: #1a5fb4;
  --dark-1: #77767b; --dark-2: #5e5c64; --dark-3: #3d3846; --dark-4: #241f31; --dark-5: #1a1a1a;
  --light-1: #ffffff; --light-2: #f6f5f4; --light-3: #deddda; --light-4: #c0bfbc;
  --radius: 12px;
  --shadow: 0 2px 8px 0 rgba(0,0,0,.14);
}

[data-theme="dark"] {
  --bg: var(--dark-4); --bg-alt: var(--dark-3);
  --fg: #fff; --fg-alt: #deddda;
}

[data-theme="light"] {
  --bg: var(--light-2); --bg-alt: var(--light-1);
  --fg: var(--dark-4); --fg-alt: var(--dark-2);
}

* { box-sizing: border-box; }
body {
  margin: 0;
  font-family: "Cantarell", "Inter", system-ui, sans-serif;
  background: var(--bg);
  color: var(--fg);
  line-height: 1.6;
  transition: background .3s ease, color .3s ease;
}
a { color: var(--blue-3); text-decoration: none; }
img { max-width: 100%; display: block; }

/* Header */
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: var(--bg-alt);
  box-shadow: var(--shadow);
  position: sticky;
  top: 0;
  z-index: 10;
}
header h1 { margin: 0; font-size: 1.5rem; }
.theme-toggle {
  cursor: pointer;
  font-size: 1.25rem;
  background: none;
  border: none;
  color: var(--fg);
}

/* Landing grid */
.grid {
  display: grid;
  gap: 2rem;
  padding: 3rem 2rem;
  max-width: 1200px;
  margin: auto;
  grid-template-columns: repeat(auto-fill,minmax(260px,1fr));
}
.card {
  background: var(--bg-alt);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  overflow: hidden;
  transition: transform .2s;
}
.card:hover { transform: translateY(-4px); }
.card img { width: 100%; height: 160px; object-fit: cover; }
.card-body { padding: 1.25rem; }
.card-body h3 { margin: 0 0 .5rem; font-size: 1.25rem; }
.card-body p  { margin: 0 0 1rem; color: var(--fg-alt); font-size: .9rem; }

/* App page hero */
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 4rem 2rem;
}
.hero img { width: 128px; height: 128px; border-radius: var(--radius); }
.hero h1 { margin: 1rem 0 .5rem; font-size: 2.5rem; }
.hero p  { font-size: 1.25rem; color: var(--fg-alt); }

.buttons { display: flex; gap: 1rem; flex-wrap: wrap; justify-content: center; margin: 2rem 0; }
.btn {
  display: inline-flex;
  align-items: center;
  gap: .5rem;
  padding: .75rem 1.5rem;
  border-radius: var(--radius);
  font-weight: 600;
  transition: .2s;
}
.btn.primary {
  background: var(--blue-3);
  color: #fff;
}
.btn.primary:hover { background: var(--blue-4); }
.btn.secondary {
  background: var(--bg-alt);
  color: var(--fg);
  border: 1px solid var(--light-4);
}
.btn.secondary:hover { background: var(--light-3); }

/* Resources */
.resources {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}
.resources h2 {
  text-align: center;
  margin: 0 0 2rem;
  font-size: 1.5rem;
  color: var(--fg);
}
.resource-links {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}
.resource-link {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: var(--bg-alt);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  transition: transform .2s, box-shadow .2s;
  text-decoration: none;
  color: var(--fg);
}
.resource-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px 0 rgba(0,0,0,.2);
  color: var(--fg);
}
.resource-icon {
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: var(--blue-3);
  border-radius: 50%;
  flex-shrink: 0;
}
.resource-info h3 {
  margin: 0 0 .25rem;
  font-size: 1rem;
  color: var(--fg);
}
.resource-info p {
  margin: 0;
  font-size: .875rem;
  color: var(--fg-alt);
}

/* Screenshots */
.screenshots {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
  padding: 0 2rem 3rem;
}
.screenshots img {
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}

/* Search and Filter Interface */
.search-filters {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background: var(--bg-alt);
  border-bottom: 1px solid var(--light-3);
}

.search-container {
  position: relative;
  margin-bottom: 2rem;
}

.search-input {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem;
  font-size: 1rem;
  border: 2px solid var(--light-3);
  border-radius: var(--radius);
  background: var(--bg);
  color: var(--fg);
  transition: border-color .2s ease;
}

.search-input:focus {
  outline: none;
  border-color: var(--blue-3);
}

.search-input::placeholder {
  color: var(--fg-alt);
}

.search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--fg-alt);
  pointer-events: none;
}

.filter-container {
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr auto;
  align-items: start;
}

.category-filters,
.tag-filters {
  margin-bottom: 1.5rem;
}

.filter-label {
  margin: 0 0 1rem;
  font-size: 1rem;
  font-weight: 600;
  color: var(--fg);
}

.filter-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.category-filter,
.tag-filter {
  padding: 0.5rem 1rem;
  border: 1px solid var(--light-3);
  border-radius: 6px;
  background: var(--bg);
  color: var(--fg);
  cursor: pointer;
  transition: all .2s ease;
  font-size: 0.875rem;
  font-weight: 500;
}

.category-filter:hover,
.tag-filter:hover {
  border-color: var(--blue-3);
  background: var(--blue-1);
}

.category-filter.active,
.tag-filter.active {
  background: var(--blue-3);
  color: white;
  border-color: var(--blue-3);
}

.clear-filters {
  padding: 0.75rem 1.5rem;
  background: var(--light-3);
  border: 1px solid var(--light-4);
  border-radius: var(--radius);
  color: var(--fg);
  cursor: pointer;
  transition: all .2s ease;
  font-size: 0.875rem;
  font-weight: 600;
  align-self: start;
}

.clear-filters:hover {
  background: var(--dark-2);
  color: white;
}

/* App Card Enhancements */
.app-meta {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--light-3);
}

.app-category {
  display: inline-block;
  padding: 0.25rem 0.5rem;
  background: var(--blue-1);
  color: var(--blue-5);
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.app-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}

.tag {
  display: inline-block;
  padding: 0.125rem 0.5rem;
  background: var(--light-3);
  color: var(--dark-3);
  border-radius: 3px;
  font-size: 0.6875rem;
  font-weight: 500;
}

.tag-more {
  display: inline-block;
  padding: 0.125rem 0.5rem;
  background: var(--dark-2);
  color: white;
  border-radius: 3px;
  font-size: 0.6875rem;
  font-weight: 500;
}

/* Results Display */
.results-count {
  text-align: center;
  padding: 1rem 2rem;
  color: var(--fg-alt);
  font-size: 0.875rem;
  font-style: italic;
}

/* Filter Animation */
.card {
  transition: opacity .2s ease, transform .2s ease, filter .2s ease;
}

.card.filtered-out {
  opacity: 0.3;
  transform: scale(0.95);
  filter: grayscale(0.5);
}

/* Dark theme adjustments */
[data-theme="dark"] {
  .search-filters {
    border-bottom-color: var(--dark-2);
  }
  
  .search-input {
    border-color: var(--dark-2);
  }
  
  .category-filter,
  .tag-filter {
    border-color: var(--dark-2);
  }
  
  .app-meta {
    border-top-color: var(--dark-2);
  }
  
  .app-category {
    background: var(--blue-4);
    color: var(--light-1);
  }
  
  .tag {
    background: var(--dark-2);
    color: var(--light-2);
  }
  
  .clear-filters {
    background: var(--dark-2);
    border-color: var(--dark-1);
  }
  
  .clear-filters:hover {
    background: var(--light-3);
    color: var(--dark-4);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-filters {
    padding: 1rem;
  }
  
  .filter-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .filter-buttons {
    gap: 0.375rem;
  }
  
  .category-filter,
  .tag-filter {
    padding: 0.375rem 0.75rem;
    font-size: 0.8125rem;
  }
  
  .clear-filters {
    width: 100%;
    text-align: center;
  }
  
  .grid {
    padding: 2rem 1rem;
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .search-input {
    padding: 0.875rem 0.875rem 0.875rem 2.5rem;
  }
  
  .search-icon {
    left: 0.875rem;
    width: 16px;
    height: 16px;
  }
  
  .filter-buttons {
    gap: 0.25rem;
  }
  
  .category-filter,
  .tag-filter {
    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
  }
}
